body {
  background: #f00;
  font-size: 2em; }

#root {
  min-height: calc(100vh - 1.5em);
  display: grid;
  grid-gap: 1em;
  padding: .5em;
  grid-template-areas: 'h' 'c' 'f'; }
  #root > * {
    background: #fff;
    padding: .5em;
    display: flex;
    align-items: center;
    justify-content: center; }
    #root > *[hidden] {
      display: none; }

header {
  grid-area: h; }

main {
  grid-area: c; }

nav {
  grid-area: n; }

footer {
  grid-area: f; }

@media screen and (max-width: 59.99em) {
  #root header {
    justify-content: space-between; }
  #root nav {
    position: fixed;
    background: chartreuse;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; } }

@media screen and (min-width: 60em) {
  #root {
    grid-template-columns: 14em 1fr;
    grid-template-areas: 'h h' 'n c' 'f f'; } }

